.header {
  grid-area: h;
  background-color: #727270;
  color: #d7d7d7;
  opacity: .98;
  position: fixed;
  width: 100%;
  user-select: none;
  z-index: 100;


  .logo {
    height: 60px;
    margin: 12px 20px;
    float: left;

    @media (max-width: 1170px) {
      margin: 18px 10px;
      height: 50px;
    }

  }

  span {
    color: white;
    float: left;
    font-size: 26px;
    margin-top: 22px;
    cursor: pointer;
    text-transform: uppercase;

    @media (max-width: 1170px) {
      font-size: 18px;
      margin: 30px 10px;
    }


  }

  .headerLinks {
    float: right;
    margin: 26px 32px 0 65px;

    .active {
      color: #f7a81a;
      position: relative;

      &:hover {
        color: #f7a81a;
        transition: 0.2s ease-in-out;
      }
      &::after {
        position: absolute;
        top: 100%;
        left: 3.5px;
        color: #f7a81a;
        content: ' ';
        width: 100%;
        height: 1.5px;
        //z-index: 101;
        background-color: #f7a81a;

      }
    }

    i {
      margin-left: 5px;
    }

    a {
      margin-left: 50px;
      color: #c9c9c9;
      text-decoration: none;
      font-size: 22px;

      @media (max-width: 1350px) {
        margin-left: 15px;
      }
      @media (max-width: 1170px) {
        font-size: 18px;
        margin-left: 15px;
      }



      &:hover {
        color: #ffffff;
        transition: 0.2s ease-in-out;
      }
    }

    @media (max-width: 1350px) {
      margin-top: 26px;
    }
    @media (max-width: 1170px) {
      margin-top: 30px;
    }

  }


}